


<template>
  <div>
    <div class='mengban' v-show="control" @click="control = false"></div>
    <div class="bkground">
      <img style="width: 100%; height: 100%;" src="../common/images/land.png">
    </div>
    <div class="land">
      <div style="padding-top: 5px;"><van-icon class="van-icon van-icon-arrow-left" /></div>
      <div class="cartIcon">
        <img style="width: 100%; height: 100%;" src="../common/images/logo@2x.png">
      </div>
      <van-tabs>
        <van-tab title="Sign In">
          <div class="fromWrap">
            <van-cell-group>
              <div class="userName" :class="{'errorShow':hide}">
                <van-field v-model="username" AUTOCOMPLETE="off" clearable placeholder="Username or Email or Phone" />
              </div>
              <div class="passwd">
                <p class="errorTips" v-show="hide"> <a class="iconfont"> &#xe657; </a>Wrong Email Address, Please check out and refill.</p>
                <van-field v-model="pwd" AUTOCOMPLETE="off" type="password" placeholder="Password"></van-field>
              </div>
            </van-cell-group>
            <span class="forgetPwd" @click="$router.push('resetPwd')">Forget Password?</span>
            <van-button type="danger" @click="login">Sign In</van-button>
            <div class="or">OR</div>
            <div class="facebook" @click="facebookLogin"> <span class="iconfont"> &#xe6d4; </span> Login with Facebook </div>
            <div class="twitter" @click="twitterLogin"> <span class="iconfont"> &#xec9c; </span> Login with Twitter</div>
            <div class="privacy">By clicking the SIGN UP button, you confirm that you accept our <font>Terms of</font> uee and <font>Privacy Policv</font> </div>
          </div>
        </van-tab>
        <van-tab title="Sign Up">
          <div class="fromUpWrap">
            <div class="personal">
              <van-field style="width: 48%;margin-right: 4%;" v-model="nickname" placeholder="Name"/>
              <div class="pwdWrap" @click="control = true">
                <van-field style="border-bottom: 1px solid #999999;" disabled @click="showGender=true" v-model="sex" placeholder="Gender" right-icon="arrow-down"/>
                <div class="witch" v-show="control">
                  <!-- <p>Men</p>
                  <p>Women</p> -->
                  <p v-for='(item,index) in genderList' class='hover' @click='genderSel(item.name)'>{{item.name}}</p>
                </div>
              </div>
            </div>
            <van-cell-group>
              <div class="telWrap" v-if="useTel" >
                <span class="areaCode">Qatar +61  <a class="iconfont cart">  &#xe671; </a></span>
                <van-field type="tel" v-model="tel" />
              </div>
              <van-field type="email" v-model="email" v-if="!useTel"  placeholder="Email"></van-field>
            </van-cell-group>
            <van-button type="danger" @click="useTel=true"   v-if="!useTel" class="switch">Use your mobile number instead</van-button>
            <van-button type="danger" @click="useTel=false"  v-if="useTel" class="switch">Use your email  instead</van-button>
            <van-cell-group>
              <van-field v-model="registerPwd" type="password" placeholder="Password"></van-field>
              <div class="invitationCode">
                <van-field v-model="invitationCode" type="text" placeholder="Invitation Code"  right-icon="question-o"  @click-right-icon="getCode = true"></van-field>
              </div>
            </van-cell-group>
            <van-button type="danger" class="btm" @click="register">Sign Up</van-button>
            <div class="privacy">By clicking the SIGN UP button, you confirm that you accept our <font>Terms of</font> uee and <font>Privacy Policv</font> </div>
          </div>
        </van-tab>
      </van-tabs>
      <div class="transparent" v-show="getCode">

      </div>
      <div class="getCode" v-show="getCode">
        <div class="photo">
          <div class="codeWrap" @click="getCode = false">
            Get Code
          </div>
          <div class="deso"><span class="iconfont back" @click="getCode = false">&#xe66c;</span></div>
        </div>

      </div>
    </div>
  </div>
</template>

<script>
  import {sendSms,sendEmail} from '@/api/msg'
  export default {
    name: 'index',
    data () {
      return {
        username: '',
        pwd: '',
        tel: '',
        nickname:"",
        sex: 'Man',
        email:'',
        invitationCode:"JJQ95AA",
        registerPwd:"",
        genders:["Man","Woman","-"],
        control: false,
        useTel:true,
        hide: false,
        genderList:[
          {name:'Men'},
          {name:'Women'},
        ],
        getCode: false,
      }
    },
    methods:{
      login(){
        this.$store.dispatch('login',{username:this.username,password:this.pwd}).then(r=>{
          this.$toast(' Sign in successfully');

        });
      },
      register(){
        let param={
          nickname:this.nickname,
          telephone:!this.useTel?"":this.tel,
          email:this.useTel?"":this.email,
          loginPassword:this.registerPwd,
          sex:this.genders.indexOf(this.sex)+1,
          invitationCode:this.invitationCode
        };

        let sendFunc=this.useTel?sendSms:sendEmail;
        sendFunc(this.username,'signup').then(res=>{
          this.$toast('Code send successfully');
          this.$router.push({path:this.useTel?'verifyMobile':'verifyEmail',query:{type:'signup',username:this.username,extra:JSON.stringify(param)}})
        });

      },
      facebookLogin(){

      },
      twitterLogin(){

      },
      genderSel(genderName){
        this.sex=genderName;
        this.control=false;
      }

    }
  }
</script>

<style>
  .land .van-tabs{
    margin-top: 15px;
  }
  .land .van-tabs__nav{
    background-color: unset;
  }
  .land .van-hairline--top-bottom::after{
    border-width:0 0 1px 0;
    border-color:rgba(255,255,255,.3)

  }
  .van-cell:not(:last-child)::after{border-color:rgba(255,255,255,.3)}
  .land .van-tab{
    color: white;
  }
  .land .van-tabs__line{
    width: 24px !important;
    left: 33px;
  }
  .land .van-tab span{ font-size: 15px;}
  .land .van-tab--active span{
    font-size: 17px;
  }
  .land .van-field__control{
    color: white;
  }
  .land .van-cell-group,.van-cell{
    background-color: unset;
  }
  .land .van-cell{
    padding: 15px 0;
  }
  .land .van-cell:not(:last-child)::after{
    left: 0;
  }
  .land .van-button{
    width: 100%;
    margin: 10px 0;
  }
  .mengban{
    width: 100%;
    height: 100%;
    position: fixed;
    z-index: 111;
    background-color: rgba(0, 0, 0, .6)
  }
  .hover:hover{
    background-color:#e33946;
  }
</style>

<style lang="scss" scoped>
  .land{
    width: 100%;
    height: 100%;
    position: relative;
  }
  .bkground{background: url('../common/images/land.png') no-repeat; width: 100%; height: 100%;position: absolute;
    width: 100vw;
    height: 100vh;
    position: absolute;
  }
  .van-icon{
    color: white;
    font-size: 19px;
    padding-left: 16px;
    padding-bottom: 5px;
  }
  .cartIcon{
    width: 69px;
    height: 69px;
    border-radius: 5px;
    margin: auto;
  }
  .signWrap{
    width: 100%;
    height: 36px;
    font-size: 15px;
    padding-top: 10px;
    color: white;
    display: -webkit-box;
  }
  .sign_in{
    width: 50%;
    display: block;
  }
  .sign_up{
    width: 50%;
    display: block;
  }
  .btm{
    border-bottom: 2px #E33946 solid;
  }
  .fromWrap{
    width: 86%;
    margin: 0 7% 0 7%;
  }
  .fromWrap p{
    color: #E33946;
  }
  .fromUpWrap{
    width: 86%;
    margin: 2% 7% 0 7%;
  }
  .forgetPwd{
    width: 100%;
    display: flex; align-items: center; justify-content: flex-end;
    color: white;
    margin-top: 5px;
    padding-bottom: 60px;
  }
  .or{
    padding: 10px 0;
    width: 100%;
    display: flex; align-items: center; justify-content: center;
    color: white;
    font-size: 17px;
  }
  .van-button__text{font-size: 15px;}
  .facebook{
    margin: 10px 0 20px 0;
    width: 100%;
    height: 45px;
    font-size: 15px;
    background:rgba(21,76,165,.7);
    display: flex;
    align-items: center;
    justify-content: center;
    color: white;
    position: relative;
  }
  .facebook .iconfont,.twitter .iconfont{
    position: absolute;
    left: 15px;
    top:50%;
    margin-top: -14px;
    font-size: 28px;
    line-height: 1em;
  }
  .facebook .iconfont{
    margin-top: -16px;
  }
  .twitter{
    width: 100%;
    height: 45px;
    font-size: 15px;
    background:rgba(120,203,239,.7);
    display: flex;
    align-items: center;
    justify-content: center;
    position: relative;
    color: white;
  }
  .privacy{
    font-family:San-Francisco-Text-Regular;
    font-size: 8px;
    color: white;
    padding-top: 5px;
  }
  .privacy font{
    color: #2B87D5;
  }
  .personal{
    width: 100%;
    display: -webkit-inline-box;

  }
  .switch{
    background:rgba(227,57,70,.5);
    border:none;
  }
  .btm{
    margin-top: 60px;
  }
  .pwdWrap{
    width: 48%;
    position: relative;
  }
  .pwdWrap .witch{
    position: absolute;
    right: 0;
    background: #fff;
    border-radius: 5px;
    box-shadow: 0 0 5px 2px rgba(23,23,23,.2);
    padding: 5px 5px 10px;
    z-index: 1000;
    height: 90px;
  }
  .pwdWrap .witch  p{
    padding:10px 10px;
    text-align: center;
    white-space: normal;
    min-width: 120px;
    font-size: 14px;
  }
  .telWrap{
    display: -webkit-box;
  }
  .areaCode{
    color: #999999;
    font-size: 14px;
    display: flex; align-items: center;
  }
  .cart{
    margin-left: 5px;
    font-size: 20px;
  }
  .telWrap .van-cell{
    width: 70%;
    padding-left: 15px;
  }
  .userName .van-cell{
    padding: 23px 0 13px 0;
  &:after{content: ""; position: absolute; left: 0; right: 0; bottom: 0; border-bottom: solid 1px #ccc;    border-width: 0 0 1px 0;  border-color: rgba(255,255,255,.3);}
  }
  .errorShow .van-cell{
  &:after{border-color:rgba(200,31,38,1)}
  }
  .passwd{padding: 23px 0 3px ; position: relative;
  .errorTips{position: absolute; left: 0; top:0;}
  }
  .passwd .van-cell{
    padding-top: 0;
  }
  .transparent{
    height: 100vh;
    width: 100vw;
    opacity: 0.5;
    z-index: 100;
    background-color: rgba(0,0,0,0.7);
    position: absolute;
    left: 0;
    top: 0;
  }
  .getCode{
    position: absolute;
    left: 0;
    top: 0;
    z-index: 100;
    height: 100vh;
    width: 100vw;
      .photo{
        position: relative;
        height: 70%;
        width: 80%;
        margin-left: 5%;
        margin-top: 40%;
        background: url('../common/images/getCode.png') no-repeat;
        background-size: 100% 73%;
        .codeWrap{
          height: 35px;
          width: 135px;
          background: url('../common/images/getcodeBtn.png') no-repeat;
          background-size: 100% 100%;
          position: absolute;
          bottom: 180px;
          left: 95px;
          font-size: 15px;
          line-height: 35px;
          text-align: center;
          color: #78482f;
        }
        .deso{
          width: 100%;
          display: flex; justify-content: center;
          position: absolute;
          bottom: 110px;
          color: white;
          margin-left: 4%;
          .iconfont{
            font-size: 25px;
          }
        }
      }
  }

</style>



